.Burger {
  width: 100%;
  height: 100%;
  z-index: 35;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba($color: #000000, $alpha: 0.7);
  visibility: visible;
  opacity: 1;
  transition: opacity 0.3s ease, visibility 0.3s ease;

  .menu {
    .title {
      display: flex;
      align-items: center;
      justify-content: space-between;

      img {
        cursor: pointer;
      }
    }

    padding: 30px;
    height: 100%;
    width: 385px;
    transform: translateX(0);
    transition: transform 0.4s ease;
    z-index: 40;
    background-color: #fff;
    position: absolute;
    right: 0;
    display: flex;
    flex-direction: column;

    .blockState {
      flex: 1 0 auto;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      img {
        width: 120px;
        height: 120px;
      }

      p {
        text-align: center;
        margin: 20px 0 40px 0 !important;
        max-width: 285px;
        color: rgba($color: #000000, $alpha: 0.5);
      }

      button {
        width: 260px;

        img {
          left: 15px;
          transform: rotate(180deg);
          width: 14px !important;
          height: 12px !important;
        }
      }
    }

    .cards {
      display: flex;
      flex-direction: column;
      flex: 1 1 auto;
      overflow-y: auto;
      overflow-x: visible;
    }

    .price {
      ul {
        padding: 0;

        li {
          display: flex;
          list-style: none;
          margin-top: 20px;

          div {
            flex: 1;
            border-bottom: 1px dashed #DFDFDF;
            margin: 0 7px 3px 7px;
          }

          h3 {
            font-weight: 600;
            font-size: 16px;
          }
        }
      }

      button {
        margin-top: 20px;
      }

    }
  }
}

.visibility {
  opacity: 0;
  visibility: hidden;
}

.scroll {
  transform: translateX(100%) !important;
  transition: transform 0.4s ease;
}